
* {
  box-sizing: border-box;
}

h1 {
  text-align: center;
}
h1 a {
  text-decoration: none;
  color: gray;
}
.menu {
  width: 100%;
  background-color: #131313;
}
nav {
  width: 60%;
  margin: 0 auto;
  display: flex;
  height: 50px;
  align-items: center;
  text-align: center;
}
nav a {
  text-decoration: none;
  color: gray;
  width: 25%;
  display: inline-block;
  color: #fff;
  line-height: 50px;
}

p {
  text-align: center;
}

nav > a {
  position: relative;
}
nav ul {
  width: 100%;
  position: absolute;
  left: 0;
  top: 50px;
  display: none;
  background-color: #131313;
}
h1, h2 {
  margin-top: 3rem;
  text-align: center;
}
.ab {
  width: 70%;
  margin: 0 auto;
}

.list {margin-top:2rem;display: flex;width:60%;margin-left:auto; margin-right:auto;}
.list li {width:25%;padding-right:5px;}
.list li img {width:100%;}
.top {margin:3rem auto;}
.top img {width:3%;}